.slider {
  cursor: pointer;
  background: linear-gradient(#dcdcdc 0%, #4f2ee8 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider:before {
  content: "";
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 18px;
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

input:checked + .slider {
  background: linear-gradient(#686868 0%, #4f2ee8 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .slider:before {
  transform: translateX(-16px);
}

.slider, .circle {
  border-radius: 24px;
}

.circle:before {
  border-radius: 50%;
}

input:checked + .circle:before {
  background-color: #fff;
}

