.loader-title {
  color: navy;
  text-align: center;
  text-transform: uppercase;
  font-family: sans-serif;
  font-size: 20px;
  position: relative;
}

.loader-title:after {
  content: "";
  animation: 2s cubic-bezier(0, .39, 1, .68) infinite Dots;
  position: absolute;
}

.loader {
  margin: 5% auto 30px;
}

.book {
  perspective: 150px;
  border: 4px solid navy;
  width: 60px;
  height: 45px;
  position: relative;
}

.page {
  transform-style: preserve-3d;
  transform-origin: 0;
  background: #00008b;
  border: 4px solid navy;
  border-left: 1px solid #00008b;
  width: 30px;
  height: 45px;
  margin: 0;
  display: block;
  position: absolute;
  top: -4px;
  right: -4px;
  overflow: hidden;
}

.book .page:first-child {
  animation: 1.2s cubic-bezier(0, .39, 1, .68) 1.6s infinite pageTurn;
}

.book .page:nth-child(2) {
  animation: 1.2s cubic-bezier(0, .39, 1, .68) 1.45s infinite pageTurn;
}

.book .page:nth-child(3) {
  animation: 1.2s cubic-bezier(0, .39, 1, .68) 1.2s infinite pageTurn;
}

@keyframes pageTurn {
  0% {
    transform: rotateY(0);
  }

  20% {
    background: #00bfff;
  }

  40% {
    background: #00008b;
    transform: rotateY(-180deg);
  }

  100% {
    background: #00008b;
    transform: rotateY(-180deg);
  }
}

@keyframes Dots {
  0% {
    content: "";
  }

  33% {
    content: ".";
  }

  66% {
    content: "..";
  }

  100% {
    content: "...";
  }
}

